import React, { Component } from "react";
import About from "./about";
import Tutorial from "./tutorial";
import AppLand from "../landing_api/Landing_page";
import Nomenclature from "./nomenclature";

import { Transition, animated } from "react-spring/renderprops";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: true,
      about: false,
      tutorial: false,
      nomenclature: false
    };
  }
  toggle_home = e =>
    this.setState({
      about: false,
      tutorial: false,
      name: false,
      home: true
    });

  toggle_name = e =>
    this.setState({
      home: false,
      about: false,
      tutorial: false,
      name: true
    });

  toggle_about = e =>
    this.setState({
      home: false,
      tutorial: false,
      name: false,
      about: true
    });

  toggle_tutorial = e =>
    this.setState({
      home: false,
      about: false,
      name: false,
      tutorial: true
    });

  render() {
    return (
      <>
        <nav className="nav" style={headerStyle}>
          <a
            className="nav-link active"
            href="http://14.139.227.206/nextrap"
            onClick={this.toggle_home}
            style={{ color: "white" }}
          >
            <badge>Home</badge>
          </a>
          <a
            className="nav-link"
            href="#"
            onClick={this.toggle_about}
            style={{ color: "white" }}
          >
            About
          </a>
          <a
            className="nav-link"
            href="#"
            onClick={this.toggle_name}
            style={{ color: "white" }}
          >
            Exon-nomenclature
          </a>
          <a
            className="nav-link"
            href="#"
            onClick={this.toggle_tutorial}
            style={{ color: "white" }}
          >
            Tutorial
          </a>
        </nav>

        <Transition
          native
          items={this.state.about}
          from={{ opacity: 0 }}
          enter={{ opacity: 1 }}
          leave={{ opacity: 0 }}
        >
          {show =>
            show &&
            (props => (
              <animated.div style={props}>
                {console.log("abconsole")}
                <About />
              </animated.div>
            ))
          }
        </Transition>
        <Transition
          native
          items={this.state.tutorial}
          from={{ opacity: 0 }}
          enter={{ opacity: 1 }}
          leave={{ opacity: 0 }}
        >
          {show =>
            show &&
            (props => (
              <animated.div style={props}>
                {console.log("tutconsole")}
                <Tutorial />
              </animated.div>
            ))
          }
        </Transition>
        <Transition
          native
          items={this.state.home}
          from={{ opacity: 0 }}
          enter={{ opacity: 1 }}
          leave={{ opacity: 0 }}
        >
          {show =>
            show &&
            (props => (
              <animated.div style={props}>
                {console.log("homeconsole")}
                <AppLand />
              </animated.div>
            ))
          }
        </Transition>
        <Transition
          native
          items={this.state.name}
          from={{ opacity: 0 }}
          enter={{ opacity: 1 }}
          leave={{ opacity: 0 }}
        >
          {show =>
            show &&
            (props => (
              <animated.div style={props}>
                {console.log("homeconsole")}
                <Nomenclature />
              </animated.div>
            ))
          }
        </Transition>
      </>
    );
  }
}
const headerStyle = {
  background: "Seagreen",
  fontWeight: 500,
  color: "Honeydew",
  padding: "0rem",
  fontFamily: "font-family: Arial, Helvetica, sans-serif",
  marginTop: "0px",
  marginBottom: "1.5rem"
};
const boldStyle = {
  color: "white",
  fontWeight: "thick"
};

export default Navbar;
