import React, { Component } from "react";
import ChooseTranscripts from "./TransExonsAlltranscripts/ChooseTranscripts";
import RenderExonsFull from "./TransExonsAlltranscripts/RenderExonsFull";
import RenderExonsAln from "./TransExonsAlltranscripts/RenderExonsAln";

export class AllTranscripts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      arraylis: [],
      exonfull: true,
      exonaln: false,
      scrollable: false,
      alert: false,
      exoninf: true
    };
    this.handleClick = this.handleClick.bind(this);
    this.showInf = this.showInf.bind(this);
    this.addAlert = this.addAlert.bind(this);
    this.removeAlert = this.removeAlert.bind(this);
  }
  toggle_exonfull = e =>
    this.setState({
      exonfull: true,
      exonaln: false
    });
  toggle_exonaln = e =>
    this.setState({
      exonfull: false,
      exonaln: true
    });
  addAlert(exid) {
    this.setState({
      alert: exid
    });
  }
  removeAlert() {
    this.setState({
      alert: false
    });
  }
  handleExonInf = e =>
    this.setState({
      exoninf: !this.state.exoninf
    });
  handleClick(tidinp) {
    let arr = [...this.state.arraylis];
    //console.log("indexof", arr.indexOf(tidinp), tidinp, arr);
    let ind = arr.indexOf(tidinp);
    //console.log("ind", ind);
    // let ind = "abc";

    if (ind !== -1) {
      arr.splice(ind, 1);
      this.setState({
        arraylis: arr
      });
    } else {
      this.setState({
        arraylis: [...this.state.arraylis, tidinp]
      });
    }
  }
  showInf(value) {
    this.setState({
      scrollable: value
    });
  }

  render() {
    const transcript = this.props.transSet;
    //console.log("passed", transcript);
    //console.log("transcriptsID added", this.state.arraylis);
    //console.log("STATE EXONX", this.state.exonfull, this.state.exonaln);
    return (
      <>
        <ChooseTranscripts
          trans={transcript}
          handleUpdate={this.handleClick}
          transadded={this.state.arraylis}
        />

        {this.state.arraylis.length > 0 ? (
          <div className="container-fluid align-items-center">
            <ul className="nav nav-tabs ">
              <li className="nav-item">
                <a href="#" onClick={this.toggle_exonfull}>
                  <span
                    className={
                      this.state.exonfull
                        ? "badge badge-dark"
                        : "badge badge-light"
                    }
                    style={{
                      fontSize: this.state.exonfull ? 20 : 15,
                      fontWeight: 500,
                      color: this.state.exonfull ? "white" : "black"
                    }}
                  >
                    Exon details
                  </span>
                </a>
              </li>
              <li className="nav-item">
                <a onClick={this.toggle_exonaln} href="#">
                  <span
                    className={
                      this.state.exonaln
                        ? "badge badge-dark"
                        : "badge badge-light"
                    }
                    style={{
                      fontSize: this.state.exonaln ? 20 : 15,
                      fontWeight: 500,
                      color: this.state.exonaln ? "white" : "black"
                    }}
                  >
                    Exon alignment
                  </span>
                </a>
              </li>
            </ul>
            {this.state.exonfull ? (
              <>
                <button
                  className="m-2 btn btn-warning btn-md"
                  onClick={this.handleExonInf}
                >
                  {this.state.exoninf
                    ? "Hide exons nomenclature"
                    : "Show exons nomenclature"}
                </button>
                <div className="row">
                  <div className="col-sm-9 border">
                    {console.log("scrollable", this.state.scrollable)}
                    {this.state.arraylis.map(tr1 => (
                      <div key={tr1.tId}>
                        <RenderExonsFull
                          transwhole={tr1}
                          domset={this.props.domset}
                          handleUpdateInfRend={this.showInf}
                          addAlert={this.addAlert}
                          removeAlert={this.removeAlert}
                          exOn={this.props.exOn}
                          exonAlerts={this.props.exonAlerts}
                        />
                      </div>
                    ))}
                  </div>

                  <div className="col-sm-3 border sticky-top">
                    <div className="d-flex flex-column sticky-top">
                      <div
                        className="p-2 bg-info"
                        style={{
                          Height: "350px"
                        }}
                      >
                        {this.showalert()}
                      </div>
                      <div className="p-2 bg-light">
                        {this.showInfFunc(this.state.scrollable)}
                      </div>
                    </div>
                  </div>
                </div>
              </>
            ) : (
              <RenderExonsAln
                arraylis={this.state.arraylis}
                total={this.props.finalExon}
                exOn={this.props.exOn}
                exonCodes={this.props.exonCodes}
              />
            )}
          </div>
        ) : null}
      </>
    );
  }

  showalert() {
    if (this.state.exoninf === false) {
      return (
        <div
          className="alert alert-light align-middle"
          style={{ height: "80px" }}
          role="alert"
        >
          <strong>Click on "Show exons nomenclature button"</strong>
        </div>
      );
    }
    if (this.state.alert) {
      return (
        <div
          className="alert alert-light"
          role="alert"
          style={{ maxHeight: "330px", minHeight: "330px", overflow: "auto" }}
        >
          {/* {this.decodeExon(this.state.alert)} */}
          {this.state.alert}
        </div>
      );
    } else {
      if (this.state.scrollable === "fasta") {
        return (
          <div
            className="alert alert-light align-middle"
            role="alert"
            style={{ height: "180px", alignItems: "middle" }}
          >
            <span className="align-middle">
              Change view for exon properties
            </span>
          </div>
        );
      }
      return (
        <div
          className="alert alert-light"
          role="alert"
          style={{ maxHeight: "330px", minHeight: "330px", overflow: "auto" }}
        >
          <span className="align-middle">Hover over exon</span>
        </div>
      );
    }
    //alert({ id });
  }
  showInfFunc(val) {
    let pfamurl = "https://pfam.xfam.org/family/";
    if (val === "ss") {
      return (
        <div className="container">
          <h5 style={{ color: "LightSlateGray" }}>
            Predicted secondary structure of exons
          </h5>
          <div className="card-body p-1 m-0">
            <span>
              <span
                className="badge badge-pill mr-3"
                style={{ background: "purple", color: "purple", width: "50px" }}
              >
                H
              </span>
            </span>

            <span>HELIX</span>
            <br />
            <span>
              <span
                className="badge badge-pill mr-3"
                style={{ background: "yellow", color: "yellow", width: "50px" }}
              >
                E
              </span>
            </span>
            <span>SHEET</span>
            <br />
            <span>
              <span
                className="badge badge-pill mr-3"
                style={{ background: "linen", color: "black", width: "50px" }}
              >
                ------
              </span>
            </span>
            <span>COIL</span>
          </div>
        </div>
      );
    } else {
      if (val === "aaseq") {
        return (
          <div className="container">
            {" "}
            <h5 style={{ color: "LightSlateGray" }}>
              Amino acid context of exons
            </h5>
          </div>
        );
      } else {
        if (val === "dom") {
          var dom = this.props.domset;
          return (
            <>
              <div className="container" style={{ padding: "0px" }}>
                <h3>Domains</h3>
                <table className="table table-sm">
                  <thead>
                    <tr>
                      <th>Color</th>
                      <th>Domain Name</th>
                      <th>Pfam ID</th>
                    </tr>
                  </thead>
                  <tbody>
                    {dom.map(ds => (
                      <tr key={ds.code}>
                        <td>
                          <span
                            className="badge badge-light"
                            style={{ background: ds.color, color: ds.color }}
                          >
                            ds.code
                          </span>
                        </td>
                        <td>{ds.dId}</td>
                        <td>
                          <a target="_blank" href={pfamurl + ds.name}>
                            {ds.name}
                          </a>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </>
          );
        } else {
          if (val === "dis") {
            return (
              <div className="container">
                <h5 style={{ color: "LightSlateGray" }}>
                  Predicted Disordered Regions of exons
                </h5>
                <span
                  className="badge badge-pill mr-3"
                  style={{ background: "olive", color: "olive", width: "50px" }}
                >
                  S
                </span>
                <span>STRUCTURED REGION</span>
                <br />
                <span
                  className="badge badge-pill mr-3"
                  style={{ background: "linen", color: "black", width: "50px" }}
                >
                  -------
                </span>
                <span>DISORDERED REGION</span>
              </div>
            );
          } else {
            return (
              <div className="container">
                <h5 style={{ color: "LightSlateGray" }}>Fasta view</h5>
              </div>
            );
          }
        }
      }
    }
  }
}

